body {
    overflow: hidden;
    margin: 0;
    padding: 0;
    background: linear-gradient(to top, #140c1a, #30023b);
    overflow-y: scroll;
    width: 100%;
    height: 100%;
}

.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    bottom: 0;
    width: 100%;
    height: 80px;
    background-color: #400050;
}

::-webkit-scrollbar {
    width: 15px;
    height: 12px;
    background: #4c005f;
}

::-webkit-scrollbar-thumb {
    width: 5px;
    height: 5px;
    border: 4px solid #4c005f;
    background: #160c1a;
    border-radius: 3px;
}